.todo {
  display: flex;
  align-items: center;
  padding: 10px;
  margin: 15px;
  font-size: 1.5rem;
  border-radius: 5px;
  border: 2px solid gray;
  color: black;
  background-color: #fff;
}

.todoText {
  width: 100%;
  text-align: left;
}

.icon {
  color: blue;
  margin-right: 5px;
}

.completedTodo {
  background-color: unset;
  border-color: gray;
  color: gray;
}

.todo.completedTodo .icon,
.todo.completedTodo .checkIcon,
.todo.completedTodo .deleteIcon {
  color: gray;
}

.checkIcon,
.deleteIcon {
  cursor: pointer;
  color: lightgrey;
  padding: 0 7px;
  font-size: 1.8rem;
  transition: transform 0.3s;
}

.checkIcon:hover,
.deleteIcon:hover {
  cursor: pointer;
  transform: scale(1.3);
}

.checkIcon:hover {
  color: green;
}

.deleteIcon:hover {
  color: red;
}
